<template>
    <div>
        <b-button v-b-modal.modal-1>Dodavanje troška</b-button>

        <b-modal id="modal-1" title="Dodavanje troška" hide-footer>
           <b-form-input v-model="iznos" placeholder="00,00kn" type="number">
           </b-form-input>
            <b-form-select v-model="kategorija" :options="sveKategorije"></b-form-select>
            <b-button @click="submit" class="mt-3" block>OK</b-button>
            <b-button @click="reset" class="mt-3" block>ODUSTANI</b-button>
        </b-modal>
    </div>
</template>

<script>
    export default {
        name: "dodavanjeTroska",
        data() {
            return {
                iznos: null,
                kategorija: null,
                sveKategorije: [
                    {
                        value: null, text: "Odaberite kategoriju", disable: true
                    },
                    {
                        value: 1, text: "Auto"
                    },
                    {
                        value: 2, text: "Hrana"
                    },
                    {
                        value: 3, text: "Kuća"
                    },
                    {
                        value: 4, text: "Mobitel"
                    },
                    {
                        value: 5, text: "Roba"
                    },
                    {
                        value: 6, text: "Shop"
                    },
                    {
                        value: 7, text: "Sport"
                    },
                    {
                        value: 8, text: "Zdravlje"
                    }
                ]
            }
        },
        methods: {
            submit: function () {
                console.log(this.iznos);
                console.log("value", this.kategorija);
                this.$store.dispatch("noviIzracun", this.iznos);
                this.$store.dispatch("trosakKategorije", this.iznos, this.kategorija);
                console.log(this.$store.state.ukupanTrosak);
                console.log(this.$store.state.autoTrosak);
            },
            reset: function () {
                this.kategorija = null;
                this.iznos = null;
            }


        }
    }
</script>

